var getWinPCT = function getWinPCT(team) {
  var _this = this;

  return new Promise(function (resolve) {
    _this.getTeam(team).then(function (res) {
      var win = res.data.ranking.matchWin;
      var loss = res.data.ranking.matchLoss;
      var winPercentage = Math.round(win / (win + loss) * 100);
      resolve({
        "data": winPercentage
      });
    });
  });
};

module.exports = getWinPCT;