var getTeam = function getTeam(id) {
  var _this = this;

  return new Promise(function (resolve) {
    var isnum = /^\d+$/.test(id);

    if (id === undefined || id === null || id === "") {
      resolve({
        "data": "Please provide a team ID or name"
      });
    } else if (!isnum) {
      _this.findTeamID(id).then(function (res) {
        resolve(_this.api.get("teams/".concat(res.data)));
      });
    } else {
      resolve(_this.api.get("teams/".concat(id)));
    }
  });
};

module.exports = getTeam;