function compareMatch(a, b) {
  return a.startDate - b.startDate;
}

owl = {
  State: {
    PENDING: 'PENDING',
    CONCLUDED: 'CONCLUDED'
  }
};

var nextMatchForTeam = function nextMatchForTeam(id) {
  var _this = this;

  return new Promise(function (resolve) {
    _this.getTeam(id).then(function (res) {
      var schedule = res.data.schedule;
      schedule.sort(compareMatch);
      return resolve({
        "data": schedule.find(function (match) {
          return match.state === owl.State.PENDING;
        })
      });
    }).catch(function (err) {
      return console.log(err);
    });
  });
};

module.exports = nextMatchForTeam;