var getGameDiff = function getGameDiff(team) {
  var _this = this;

  return new Promise(function (resolve) {
    _this.getTeam(team).then(function (res) {
      var win = res.data.ranking.gameWin;
      var loss = res.data.ranking.gameLoss;
      var diff = win - loss;
      resolve({
        "data": diff
      });
    });
  });
};

module.exports = getGameDiff;